/* This trump is based/copy of Svenskttenn */

.t-show-for-medium-up.t-show-for-medium-up,
.t-show-for-medium-up-block.t-show-for-medium-up-block,
.t-show-for-medium-up-inline-block.t-show-for-medium-up-inline-block,
.t-show-for-medium-up-table-cell.t-show-for-medium-up-table-cell,
.t-show-for-lg.t-show-for-lg,
.t-show-for-lg-up.t-show-for-lg-up,
.t-show-for-xl.t-show-for-xl,
.t-show-for-xxl.t-show-for-xxl,
.t-show-for-lg-plus.t-show-for-lg-plus {
	display:none;
}

@media(--md) {
	.t-show-for-small.t-show-for-small {
		display:none;
	}

	.t-show-for-medium-up.t-show-for-medium-up {
		display:inherit;
	}

	.t-show-for-medium-up-block.t-show-for-medium-up-block {
		display:block;
	}

	.t-show-for-medium-up-inline-block.t-show-for-medium-up-inline-block {
		display:inline-block;
	}

	.t-show-for-medium-up-table-cell.t-show-for-medium-up-table-cell {
		display:table-cell;
	}
}

@media(--lg) {
	.t-show-on-md-down.t-show-on-md-down {
		display:none;
	}

	.t-show-for-lg.t-show-for-lg {
		display:inherit;
	}

	.t-show-for-lg-up.t-show-for-lg-up {
		display:inherit;
	}
}

@media(--xl) {
	.t-show-for-xl.t-show-for-xl {
		display:inherit;
	}
}