/* =============================================================================
 * Editor selectable classes
 * These class names will show up in a dropdown in the WYSIWYG editor in Episerver.
============================================================================= */

/* Text alignment
------------------------------------- */
.t-text-align-left {
	EditMenuTitle:Text alignment;
	EditMenuName:Left;
}

.t-text-align-center {
	EditMenuName:Center;
}

.t-text-align-right {
	EditMenuName:Right;
}

/* Whitespace
------------------------------------- */
.t-clear {
	EditMenuTitle:Whitespace;
	EditMenuName:Clear;
}

.t-nowrap {
	EditMenuName:No wrap;
}


/* Images
------------------------------------- */
img.t-float-left {
	EditMenuTitle:Image;
	EditMenuName:Left aligned;
}

img.t-float-right {
	EditMenuName:Right aligned;
}
