/*
 * Text styles
 */
@define-mixin text-style-reset {
	background:transparent;
	color:inherit;
	font:var(--type-defaults);
	line-height:var(--type-line-height-default);
	text-transform:none;
	text-shadow:none;
	word-spacing:normal;
	letter-spacing:normal;
}

@define-mixin text-style-alpha {
	@mixin text-style-reset;
	font-size:calc(var(--type-size-default) * 2);
	line-height:1;
}

@define-mixin text-style-beta {
	@mixin text-style-reset;
	font-size:calc(var(--type-size-default) * 1.5);
	line-height:1;
}

@define-mixin text-style-gamma {
	@mixin text-style-reset;
	font-style:italic;
	font-size:calc(var(--type-size-default) * 0.8);
}

/*	HEADLINES STYLES */

@define-mixin text-style-h1 {
	@mixin text-style-reset;
	@media (--lg) {
		font-size: calc(var(--type-size-default) * 4.2);
	}
	font-size: calc(var(--type-size-default) * 3.2);
	font-family: var(--type-family-headline);
	line-height:1.3
}

@define-mixin text-style-h2 {
	@mixin text-style-reset;
	color: #045389;
	font-weight: 300;
	font-size:calc(var(--type-size-default) * 2.8);
	font-family: var(--type-family-headline);
	line-height:1.3;
}

@define-mixin text-style-h3 {
	@mixin text-style-reset;
	margin-bottom: 0.5em;
	color: #045389;
	font-size:calc(var(--type-size-default) * 1.5);
	font-family: var(--type-family-headline);
	line-height:1.3;
}

@define-mixin text-style-h4 {
	@mixin text-style-reset;
	color: #045389;
	font-size:calc(var(--type-size-default) * 1.2);
	font-family: var(--type-family-headline);
	line-height:1.3;
}

@define-mixin text-style-h5 {
	@mixin text-style-reset;
	color: #3c3c3c;
	font-weight: bold;
	font-size:calc(var(--type-size-default) * 1);
	line-height:1;
}

@define-mixin text-style-blockquote-large {
	@mixin text-style-reset;
	font-weight: 400;
	font-size:calc(var(--type-size-default) * 2.5);
	font-family: var(--type-family-headline);
	line-height:1.4;
}

@define-mixin text-style-hero-h2 {
	@mixin text-style-reset;
	font-size:calc(var(--type-size-default) * 2.8);
	line-height:1.4;
}

@define-mixin text-style-hero-size {
	@mixin text-style-reset;
	font-size:calc(var(--type-size-default) * 1.3);
	line-height:1.4;
}
