/* =============================================================================
 * Text styles
============================================================================= */
.t-text-style-alpha.t-text-style-alpha {
	@mixin text-style-alpha;
}

.t-text-style-beta.t-text-style-beta {
	@mixin text-style-beta;
}

.t-text-style-gamma.t-text-style-gamma {
	@mixin text-style-gamma;
}

.t-text-style-h1[class] {
	@mixin text-style-h1;
}

.t-text-style-h2[class] {
	@mixin text-style-h2;
}

.t-text-style-h3[class] {
	@mixin text-style-h3;
}

.t-text-style-hero-h2[class] {
	@mixin text-style-hero-h2;
}
