/* =============================================================================
 * Text scope
 *
 * Adds styling to elements when inside the scope .s-text, e.g. margin
 * or another font-weight than 'normal'.
============================================================================= */

/* Margins
 * Weird syntax for clarity's sake.
 ---------------------------------------------------------------------------- */
.s-text h1,
h1.s-text,
.s-text h2,
h2.s-text,
.s-text h3,
h3.s-text,
.s-text h4,
h4.s-text,
.s-text h5,
h5.s-text,
.s-text h6,
h6.s-text,
.s-text ul,
ul.s-text,
.s-text ol,
ol.s-text,
.s-text dl,
dl.s-text,
.s-text blockquote,
blockquote.s-text,
.s-text p,
p.s-text,
.s-text address,
address.s-text,
.s-text hr,
hr.s-text,
.s-text table,
table.s-text,
.s-text fieldset,
fieldset.s-text,
.s-text figure,
figure.s-text,
.s-text pre,
pre.s-text {
	margin-bottom: var(--spacing-2);
}


/* Headings
 ---------------------------------------------------------------------------- */
.s-text h1,
h1.s-text {
	@mixin text-style-h1;
}

.s-text h2,
h2.s-text {
	@mixin text-style-h2;
}

.s-text h3,
h3.s-text {
	@mixin text-style-h3;
}

.s-text h4,
h4.s-text {
	@mixin text-style-h4;
}

.s-text h5,
h5.s-text {
	@mixin text-style-h5;
}

.s-text h6,
h6.s-text {
	@mixin text-style-h5;
}

/* Inverted heading colors
 ---------------------------------------------------------------------------- */
 .s-text--inverted,
 .s-text--inverted a,
 .s-text--inverted h1,
 h1.s-text--inverted,
 .s-text--inverted h2,
 h2.s-text--inverted,
 .s-text--inverted h3,
 h3.s-text--inverted,
 .s-text--inverted h4,
 h4.s-text--inverted,
 .s-text--inverted h5,
 h5.s-text--inverted,
 .s-text--inverted h6,
 h6.s-text--inverted {
 	color: #fff;
 }

/* Inline typography
 ---------------------------------------------------------------------------- */
.s-text i,
i.s-text,
.s-text em,
em.s-text {
	font-style:italic;
}

.s-text b,
b.s-text,
.s-text strong,
strong.s-text {
	font-weight:700;
}


/* Lists
 *
 * 1. Remove trailing margins from nested lists.
 ---------------------------------------------------------------------------- */
.s-text ul,
ul.s-text {
	list-style:disc;
}

.s-text ol,
ol.s-text {
	list-style:decimal;
}

.s-text ul,
ul.s-text,
.s-text ol,
ol.s-text,
.s-text dd,
dd.s-text {
	margin-left:20px;
}

.s-text li {
	margin-bottom:5px;
}

.s-text li > ul,
.s-text li > ol {
	margin-top:5px;
	margin-bottom:0; /* [1] */
}

.s-text dt {
	font-weight:700;
}

.s-text dd {
	margin-bottom:10px;
}


/* Tables
 *
 * 1. To counter-act top padding on th
 ---------------------------------------------------------------------------- */
.s-text table,
table.s-text {
	margin-top:-5px; /* [1] */
}

.s-text caption,
caption.s-text {
	padding-bottom:15px;
	border-bottom:1px solid #ccc;
}

.s-text th,
th.s-text {
	padding:5px 20px 5px 0;
	border-bottom:1px solid #ccc;
}

.s-text td,
td.s-text,
.s-text tbody th,
tbody th.s-text {
	padding:15px 20px 15px 0;
	border-bottom:1px solid #ccc;
}

.s-text tfoot,
tfoot.s-text {
	font-weight:bold;
}

/* Images
 ---------------------------------------------------------------------------- */
 .s-text img,
 img.s-text {
	 margin-bottom: var(--spacing-3);
 }
