/* =============================================================================
 * Variables
 *
 * Colour and type related variables.
============================================================================= */
:root {
	/* COLORS */
	--color-white:#fff;
	--color-black:#000;
	--color-red:#f00;
	--color-blue:#045389;
	--color-light-gray:#ccc;
	--color-gray:#999;
	--color-gamma:#00f;
	/* FONT FAMILIES */
	--type-family-default:Arimo, Helvetica, sans-serif;
	--type-family-headline:Oswald;
	/* FONT SIZES */
	--type-size-default:16px;
	/* LINE HEIGHTS */
	--type-line-height-default:1.5;
	/* FONT CONFIGURATIONS */
	--type-defaults:400 var(--type-size-default)/var(--type-line-height-default) var(--type-family-default);
	/* SPACING */
	--spacing-0:8px;
	--spacing-1:calc(var(--spacing-0) * 2); /* 16 */
	--spacing-2:calc(var(--spacing-0) * 3); /* 24 */
	--spacing-3:calc(var(--spacing-0) * 4); /* 32 */
	--spacing-4:calc(var(--spacing-0) * 5); /* 40 */
	--spacing-5:calc(var(--spacing-0) * 6); /* 48 */
	--spacing-6:calc(var(--spacing-0) * 7); /* 56 */
}
